.App {
  text-align: center;
  overflow:scroll;
  height:100vh;
  background-color:black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.logo-container{
  display:flex;
  flex-direction:column;
  align-items: center;
}
.logo-container svg{
  border:1px solid #f7033e;
  border-radius:10px;
}
.logo-container svg rect{
  transform-origin:center;
  stroke:none;
}
.logo-container svg .top{
  transform:rotate(-2deg);
  fill:#219dc5;
}
.logo-container svg .mid{
  transform:rotate(-7.5deg);
  fill:#f7033e;
}
.logo-container svg .bottom{
  transform:rotate(3deg);
  fill:#00cd80;
}
.second{
  display:flex;
  flex-direction: column;
}
.code-block{
  padding:5px 10px;
  margin:5px 10%;
  width:80%;
  color:rgb(231, 241, 253);
  background-color:rgba(0,0,0,0.65);
}
.space-after-container{
  display:flex;
  flex-direction:column;
  align-items: center;
  margin-bottom:25px;
}
.space-after-container input{
  width:250px;
}
.radio-container{
  margin-top:25px;
}
.radio-container,
.checkboxes-container{
  display:flex;
  justify-content: center;
  margin-bottom:15px;
}
input[type="radio"],
input[type="checkbox"]{
  opacity:0;
  position:fixed;
  width:0;
}
input[type="radio"]:checked + label,
input[type="checkbox"]:checked + label{
  background-color:lightgreen;
  color:black;
}
input[type="range"]{
  background:rgba(0,0,0,0.65);
}
.radio-label,
.checkbox-label{
  border:2px solid white;
  padding:5px 10px;
  margin-bottom:10px;
}
.display-code{
  background-color:rgba(0,0,0,0.65);
  margin:0px 10px;
  padding:10px 0px;
}